import React from 'react';
import { useQuery } from 'react-query';
import { ThemeProvider, useTheme } from './theme';
import { Theme } from './theme/types';
import { Link } from 'react-router-dom';
import { get } from './service';
import {
  Pokemon as PokemonResponse,
  PokemonResource,
} from './model/ResponseTypes';

const { innerWidth } = window;
const IS_MOBILE = innerWidth <= 480;

type GridCardProps = {
  resource: PokemonResource;
  style?: any;
};

const Error = ({ error }: any) => <div>Error: {error.message}</div>;

const makeStyles = ({ palette: colors, spacing }: Theme) => {
  // TODO: figure out best way to do mobile styles
  const cardSize = IS_MOBILE ? 112 : 128;
  const margin = IS_MOBILE ? 0 : spacing * 2;
  const spinnerSize = IS_MOBILE ? 45 : 60;
  // TODO: put these in theme
  const cardBackground = '#555';
  const cardBorderColor = '#222';
  const nameTitleTopBorder = '#aaa';
  const nameTitleBackground = '#888';

  return {
    card: {
      alignItems: 'flex-end',
      backgroundColor: cardBackground,
      backgroundPositionX: 'center',
      backgroundRepeat: 'no-repeat',
      border: `2px solid ${cardBorderColor}`,
      color: colors.text.primary,
      display: 'flex',
      height: cardSize,
      imageRendering: 'pixelated',
      justifyContent: 'center',
      lineBreak: 'normal',
      margin: spacing / 2,
      textDecoration: 'none',
      width: cardSize,
    },
    name: {
      backgroundColor: nameTitleBackground,
      borderTop: `2px solid ${nameTitleTopBorder}`,
      padding: spacing,
      width: '100%',
    },
  };
};

export default function GridCard({ resource, style }: GridCardProps) {
  const styles = makeStyles(useTheme().state);
  const { isLoading, error, data } = useQuery<PokemonResponse, Error>(
    resource.url,
    () => get(resource.url),
  );
  if (error) {
    return <Error error={error} />;
  }

  const pokemon: PokemonResponse | undefined = data;
  const pokemonName = pokemon
    ? pokemon.name.replace(/^\w/, (l) => l.toUpperCase())
    : '';

  console.log('here', style);
  const Container = ({ children }: any) =>
    !!style ? (
      <section
        style={{
          ...styles.card,
          backgroundImage: `url(${pokemon?.sprites.front_default})`,
          ...style,
        }}
      >
        {children}
      </section>
    ) : (
      <Link
        style={{
          ...styles.card,
          backgroundImage: `url(${pokemon?.sprites.front_default})`,
          ...style,
        }}
        to={`/${pokemon?.id}`}
      >
        {children}
      </Link>
    );

  return (
    <Container>
      {isLoading ? (
        <article style={styles.name}>Loading...</article>
      ) : (
        <article style={styles.name}>{pokemonName}</article>
      )}
    </Container>
  );
}
